@import url(https://use.fontawesome.com/releases/v5.8.1/css/all.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loader,
.loader:before,
.loader:after {
  background: #0080ff;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: #0080ff;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  transform: translateZ(0);
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
h1, h2{
    font-size: 2.618em;
    text-shadow: 0 2px 20px rgba(0,0,0,.40);
    text-transform: capitalize;
}
nav.nav-references{
    display: none;
}

button.burger-button{
    width: 30px;
    background-color: transparent;
    padding: 0;
    border: none;
    font-size: 50px;
    cursor: pointer;
    color: white;
    transition: all .5s ease-in-out;
    margin: 20px;
}

button.burger-bottom:hover{
    -webkit-transform: scale(1.05);
}

.nav-references p{
    margin: 0;
    padding: 5px;
}

@media(min-width: 768px){

    button.burger-button{
        display: none;
    }

    nav.nav-references{
        display: flex!important;
    }

    .nav-references p{
        padding: 15px;
    }
}



p.url-link a{
    text-decoration: none;
    color: white;
    font-size: 1.3rem;
    position: relative;
    text-shadow: 0 2px 20px rgba(0,0,0,.40);

}

p.url-link:hover a:after{
    visibility: visible;
    transform: scaleX(1);
}

a{
    transition: all .5s ease-in-out;
}

p.url-link:hover a{
    cursor: pointer;
}

.url-link a:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0px;
    left: 0;
    background-color: #0033cc;
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
}

.logo-and-name{
    display: flex;
    align-items: center;
}

.logo-and-name img{
    width: 80px;
}

.references-var{
    margin: auto;
    max-width: 1400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.site-header{
    background-image: url(/react-landpage/static/media/waveResponsive.20ecd630.svg);
    background-size: cover;
    background-position: center center;
    padding: 0 10px;
    height: 50vh;
}

.header-text{
  text-align: center;
  color: white;
  margin-top: 2.5%;
}

.videogame-image-header img{
    display: none;
}

@media(min-width: 768px){
    .videogame-image-header img{
        display: block;
        max-width: 500px;
        width: 60%;
        margin-left: auto;
    }

    .header-text{
        float: left;
        max-width: 40%;
        text-align: left;
    }

    .site-header{
        padding-bottom: 60px;
        height: 700px;
        background-image: url(/react-landpage/static/media/wave5.ce959b69.svg);
    }
}

.logo-and-name h2{
    font-size: 2.3em;
    color: aliceblue;
}




.container{
    margin: auto;
    max-width: 1200px;
}

html{
    box-sizing: border-box;
}
*, *:before, *:after{
    box-sizing: inherit;
}

body{
    font-family: 'PT Sans', sans-serif;
}

.clearfix:before,
.clearfix:after {
  content: " "; /* 1 */
  display: table; /* 2 */
}

.clearfix:after {
  clear: both;
}


.single-property{
    text-align: center;
    margin: 20px 0;
    padding: 20px 0;
}

@media(min-width: 768px){
    .single-property{
        float: left;
        width: 33.3%;
    }
}

.single-property img{
    width: 100%;
    max-height: 200px;
}
.title h2, .site-games-list h2{
    text-align: center;
    color: rgb(102, 0, 204);
    font-style: italic;
    margin: 0;
}

.title{
    position: relative;
    background:white;
    padding-bottom:4px;
    box-sizing: border-box;
    margin: auto;
}

.title:after{
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: -1;
    margin-bottom: -3px;
    background: linear-gradient(0.25turn, rgba(251,63,244), rgba(67,89,252), rgba(252,70,70,0));
}

.dis-flex{
    display: flex;
    margin: 10px 0;
}

.list-of-properties{
    margin: 70px 0;
}

.site-properties{
    padding-bottom: 50px;
}

.single-feature{
    margin: 20px;
    padding: 20px;
}

.feature-img img{
    width: 100%;
    max-height: 300px;
}

.feature-text{
    padding: 40px;
}

@media(min-width: 768px){

    .feature-img{
        width: 60%;
    }

    .single-feature{
        display: flex;
        justify-content: space-evenly;
        padding: 70px 0;
    }

    .feature-text{
        width: 45%;
        padding: 0 4%;
    }
}


.main-features{
    position: relative;
    background-color: rgb(224, 253, 251);
    box-shadow: -1px 6px 15px -9px rgba(69,235,247,1);
}

.main-features svg{
    position: absolute;
    bottom: 100%;
}

.site-features{
    padding-bottom: 30px;
}

.site-features h2{
    text-align: center;
    padding: 30px;
}
.single-game img{
    width: 100%;
    max-width: 150px;
    display: block;
    margin: 0 auto;
    border-radius: 15%;
}

.single-game{
    padding: 10px;
    margin: 10px;
    box-shadow: -1px 0px 11px -3px rgba(0,0,0,0.63);
    border-radius: 7px;
    background-color: white;
}

.single-game h3{
    text-align: center;
    text-transform: capitalize;
    font-size: 1.5rem;
}

.single-game p{
    margin: 5px 0;
}

.single-game .mini-likes-comments{
    display: flex;
    justify-content: space-between;
}

.game-info a{
    text-decoration: none;
    color: black;
}

.mini-likes-comments{
    font-size: 1.1rem;
}

.mini-likes-comments .heart-color{
    color: #dd0a50;
}

.mini-likes-comments .comment-color{
    color: burlywood;
}

.gameLayout-username{
    font-size: 1.3rem;
    margin: 15px 0;
}
.site-games-list h2{
    text-align: center;
    padding: 1em 0 0 0;
}

.arrows button{
    color: black;
}

.list-background{
    margin: 20px 0;
}

.site-games-list{
    padding: 30px 0;
}
footer{
    margin-top: 30px;
    position: relative;
    
}

@media(min-width: 768px){
    footer{
        margin-top: 9rem;
    }
}
.site-footer{
    background: linear-gradient(180deg, rgba(254,108,248,1) 1%, rgba(151,243,254,1) 100%);
    padding-bottom: 20px;
}

.site-footer .container{
    text-align: center;
}

footer svg{
    position: absolute;
    bottom: 100%;
}

.logo-name div{
    justify-content: center;
}

@media(min-width: 768px){
    .links-info{
        display: flex;
        justify-content: space-around;
    }
}

.footer-info, #copyright-zone{
    font-size: 1.3em;
}

.site-login{
    margin: 0 auto;
    max-width: 450px;
    padding: 20px;
    background: linear-gradient(121deg, rgba(106,124,255,1) 0%, rgba(231,169,255,1) 100%);
    border-radius: 10px;
}

@media(min-width: 450px){
    .site-login{
        margin-top: 8%;

    }
}

.login-letter{
    margin: 10px;
    padding: 20px;
}

.login-letter img{
    display: block;
    margin: 0 auto;
    max-width: 200px;
}

.login-letter h1{
    margin-top: 0;
    text-align: center;
}

.login-form .campo{
    margin: 40px 20px;
}

.login-form .campo:first-of-type{
    margin: 20px;
}

.login-form .campo input{
    width: 100%;
    padding: 10px;
    background-color: transparent;
    border: none;
    border-bottom: 1.3px solid white;
}

.login-form .campo input::-webkit-input-placeholder{
    color: white;
}

.login-form .campo input:-ms-input-placeholder{
    color: white;
}

.login-form .campo input::placeholder{
    color: white;
}

.login-form .campo input:focus{
    outline: none;
    border-bottom: 2px solid white;
}





.form-button{
    padding: 10px;
    border-radius: 10px;
    font-size: 20px;
    text-decoration: none;
    font-weight: bold;
    transition: all .5s ease-in-out;
    border: none;
    color: black;
    background-color: white;
    display: block;
    margin: 40px auto;
}

button.form-button:hover{
    -webkit-transform: scale(1.1);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
    cursor: pointer;
}

